<template>
<section class="hero is-dark is-fullheight is-bold" id="index">
  <div class="hero-body">
    <div class="container has-text-centered">
      <h1 class="title is-size-1 is-size-3-mobile">
        T U R B U L E N S S I
      </h1>
      <div class="columns is-centered">
        <div class="column is-narrow">
          <h2 class="title is-size-1">
            1961
          </h2>
        </div>
        <div class="column is-narrow">
          <figure class="image container is-48x48">
              <img src="@/assets/valkoinen_nuoli.png">
          </figure>
        </div>
        <div class="column is-narrow">
          <h2 class="title is-size-1">
            2021
          </h2>
        </div>
      </div>
      <transition name="fade">
        <div v-if="nowDate <= countDownDate">
          <p id="timer">
            {{ days }} päivää {{ hours }} tuntia {{ minutes }} minuuttia ja {{ seconds }} sekunttia
          </p>
        </div>
      </transition>
    </div>
  </div>
</section>
</template>

<script>
  export default {
      name: 'Index',
      data() {
        return {
          nowDate: new Date().getTime(),
          countDownDate: new Date("Mar 19, 2022 00:00:00").getTime(),
          days: 0,
          hours: 0,
          minutes: 0,
          seconds: 0
        }
      },
      mounted() {
        this.countDown();
        setInterval(() => {
          this.countDown();
        }, 1000);
      },
      methods: {
        countDown() {
          this.nowDate = new Date().getTime();
          let distance = this.countDownDate - this.nowDate;

          this.days = Math.floor(distance / (1000 * 60 * 60 * 24));
          this.hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
          this.minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
          this.seconds = Math.floor((distance % (1000 * 60)) / 1000);
        }
      }
  }
</script>

<style scoped>
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
</style>
